.header {
    background: transparent;
    color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(1px);
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    width: 100%;
    max-height: 60px;
    z-index: 1000;
    position: absolute;
    top: 0;
    left: 0;

}
.nav-link.active-tab {
    color: #cf30aa;
    border-bottom: 2px solid #cf30aa;
    font-weight: bold;
}

.left-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 1rem;
}

.right-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 1rem;
}

.menu-container {
    align-items: center;
    cursor: pointer;
}

.menu-container span {
    font-size: 16px;
    color: white;
}

.menu-container:hover {
    font-size: 16px;
    color: var(--color-primary-fire);
}

.slide-menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    background: var(--background-gradient-start);
    box-shadow: 4px 0 15px rgba(0, 0, 0, 0.2);
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    z-index: 1500;
    display: flex;
    flex-direction: column;
    padding: 20px;
    box-sizing: border-box;
}

.slide-menu.show {
    transform: translateX(0);
}

.menu-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1400;
    background: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s ease-in-out;
    opacity: 1;
}

.menu-item {
    padding: 12px 16px;
    font-size: 16px;
    color: white;
    text-decoration: none;
    border-bottom: 1px solid #eaeaea;
}

.menu-item:hover {
    background-color: var(--color-primary-fire);
    color: #000;
}
.menu-item:active {
    background-color: var(--color-primary-fire);
    color: #000;
}



.logo-image {
    width: 15rem;
    height: auto;
    max-height: 60%;
    max-width: 60%;
}


.nav-icons {
    display: flex;
    align-items: center;
    gap: 0.1rem;
    position: relative;
}

.nav-icons:hover {
    color: var(--color-primary-fire);
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 1rem;
    flex-grow: 1;
}

.nav-link {
    color: white;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.nav-link:hover {
    color: var(--color-primary-fire);
}
.nav-link:active {
    color: var(--color-primary-fire);
}

.user-controls {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
    cursor: pointer;
}


.dropdown-menu {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.8);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    padding: 1% 0.5%;
    right: 0;
    top: 100%;
    z-index: 1500;
    border-radius: 4px;
}

.dropdown-item {
    padding: 8px;
    cursor: pointer;
    color: white;
}

.dropdown-item:hover {
    background-color: var(--color-primary-fire);
    color: black;
}

@media (max-width: 768px) {
    .nav-links {
        display: none;
    }

    .menu-container {
        display: flex;
    }

    .mobile-nav {
        display: block;
        position: relative;
        width: 200%;
    }

    .mobile-nav:hover {
        color: var(--color-primary-fire);
    }

    .mobile-nav-menu {
        position: absolute;
        background-color: rgba(0, 0, 0, 0.8);
        width: auto;
        left: 0;
        top: 100%;
        z-index: 1500;
    }
    .logo-image {
        left: 0;
        width: 100rem;
        height: auto;
        max-height: 60%;
        max-width: 60%;
    }

    .mobile-nav-menu.show {
        display: block;
    }

    .mobile-nav-item {
        padding: 12px 16px;
        color: white;
        text-decoration: none;
        display: block;
    }

    .mobile-nav-item:hover {
        background-color: var(--color-primary-fire);
        color: black;
    }

    .header {
        padding: 0 10px;
        height: 50px;
    }



    .right-container {
        gap: 0.5rem;
    }

    .menu-container span {
        font-size: 14px;
    }

    .search-icon {
        width: 1.5rem;
        height: 1.5rem;
    }
}

@media (min-width: 768px){
    .mobile-nav{
        display: none;
    }
}
.search-icon {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
@media (max-width: 992px) {
    .header {
        padding: 0 15px;
        height: 55px;
    }

    .nav-link {
        font-size: 14px;
    }

    .right-container {
        gap: 0.5rem;
    }
}
@media (max-width: 576px) {
    .header {
        padding: 0 5px;
        height: 45px;
    }

    .logo-text {
        font-size: 0.875rem;
    }

    .menu-container span {
        font-size: 12px;
    }

    .search-icon {
        width: 1.25rem;
        height: 1.25rem;
    }

    .mobile-nav-item {
        padding: 10px 12px;
        font-size: 14px;
    }
}
