
.home-page {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.genre-selector {
    margin: 20px;
    text-align: center;
}

.genre-selector select {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}


.home_content {
    width: 100%;
    box-sizing: border-box;
    flex-grow: 1;

}
/* StarryBackground.css */
.starry-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: black;
    overflow: hidden;

}
