* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
:root {
    --color-highlight-fire: rgb(255, 236, 163);
    --color-primary-fire: #cf30aa;
    --color-secondary-fire: rgba(240, 96, 29, 1);
    --color-highlight-ice: rgba(168, 236, 255, 1);
    --color-primary-ice: rgb(97, 214, 250);
    --color-secondary-ice: rgb(58, 91, 161);
    --background-gradient-start: #010101;
    --background-gradient-end: #0a0000;
    --background: 0 0% 100%;
    --foreground: 240 10% 3.9%;
    --card: 0 0% 100%;
    --card-foreground: 240 10% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 240 10% 3.9%;
    --primary: 240 5.9% 10%;
    --primary-foreground: 0 0% 98%;
    --secondary: 240 4.8% 95.9%;
    --secondary-foreground: 240 5.9% 10%;
    --muted: 240 4.8% 95.9%;
    --muted-foreground: 240 3.8% 46.1%;
    --accent: 240 4.8% 95.9%;
    --accent-foreground: 240 5.9% 10%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 0 0% 98%;
    --border: 240 5.9% 90%;
    --input: 240 5.9% 90%;
    --ring: 240 5.9% 10%;
    --radius: 0.5rem;
}

html, body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    scrollbar-width: none;
}

#root {
    width: 100%;
    height: 100%;
    margin: 0;

}
#player {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black; /* Optional: background color */
}
